<template>
  <div data-class="minesweeper">
    <h1 data-class="minesweeper-title">
      „Éû„Ç§„É≥„Çπ„Ç§„Éº„Éë
    </h1>

    <div data-class="minesweeper-start-buttons">
      <button class="start-button" @click="start(9, 9, 10)">
        ü•öÂàùÁ¥öü•ö
      </button>
      <button class="start-button" @click="start(16, 16, 40)">
        üê§‰∏≠Á¥öüê§
      </button>
      <button class="start-button" @click="start(16, 30, 99)">
        üêì‰∏äÁ¥öüêì
      </button>
    </div>
    
    <div style="margin: 1rem 0;"></div>

    <table data-class="minesweeper-board" class="minesweeper-board">
      <template v-for="(row, xi) in area">
        <tr :key="xi">
          <template v-for="(cell, yi) in row">
            <td
              v-if="started"
              :key="yi"
              class="cell cell--started"
              :class="{ 'cell--opened': cell.isOpen }"
              @click="open(xi, yi)"
              @click.right.prevent="mark(xi, yi)"
            >
              <span v-if="cell.isOpen">
                {{ cell.isBomb ? "üí£" : cell.bombCount === 0 ? "" : cell.bombCount }}
              </span>
              <span v-if="cell.isMark">
                üö©
              </span>
            </td>
            <td
              v-if="!started"
              class="cell cell--unstarted"
              :key="yi"
            >
              &nbsp;
            </td>
          </template>
        </tr>
      </template>    
    </table>
  </div>
</template>

<script>
/**
 * Áõ§Èù¢‰∏ä„ÅÆ„Çª„É´.
 */
class Cell {
  /**
   * Èñã„ÅÑ„Å¶„ÅÑ„Çã„Åã.
   * @type {Boolean}
   */
  isOpen = false;

  /**
   * Âú∞Èõ∑„ÅåÁΩÆ„Åã„Çå„Å¶„ÅÑ„Çã„Åã.
   * @type {Boolean}
   */
  isBomb = false;

  /**
   * Êóó„ÅåÁ´ã„Å¶„Çâ„Çå„Å¶„ÅÑ„Çã„Åã.
   * @type {Boolean}
   */
  isMark = false;

  /**
   * Âë®Âõ≤„ÅÆÂú∞Èõ∑„ÅÆÊï∞.
   * @type {Number}
   */
  bombCount = 0;

  /**
   * Ëá™Âãï„ÅßÈñã„Åè„Åì„Å®„Åå„Åß„Åç„Çã„ÅãÂà§ÂÆö„Åó„Å¶Ëøî„Åó„Åæ„Åô.
   * @return {Boolean}
   */
  autoOpenable() {
    return !this.isOpen && !this.isBomb;
  }
}

export default {
  name: "Minesweeper",

  data() {
    return {
      /**
       * „Ç≤„Éº„É†„ÅåÈñãÂßã„Åï„Çå„Åü„Åã.
       * @type {Boolean}
       */
      started: false,

      /**
       * Áõ§Èù¢.
       * @type {Array}
       */
      area: [],
      
      /**
       * Áõ§Èù¢„ÅÆË°åÊï∞.
       * @type {Number}
       */
      maxX: 9,
      
      /**
       * Áõ§Èù¢„ÅÆÂàóÊï∞.
       * @type {Number}
       */
      maxY: 9,
      
      /**
       * Âú∞Èõ∑„ÅÆÊï∞.
       * @type {Number}
       */
      numOfBomb: 0,
    }
  },

  created() {
    this._clearBoard();
  },

  methods: {
    /**
     * „Éû„Ç§„É≥„Çπ„Ç§„Éº„Éë„ÇíÈñãÂßã„Åó„Åæ„Åô.
     * @param {Number} maxX Ë°åÊï∞.
     * @param {Number} maxY ÂàóÊï∞.
     * @param {Number} numOfBomb Âú∞Èõ∑„ÅÆÊï∞.
     */
    start(maxX, maxY, numOfBomb) {
      this.started = false;

      this.maxX = maxX;
      this.maxY = maxY;
      this.numOfBomb = numOfBomb;
      
      this._clearBoard();
      this._layMines();

      this.started = true;
    },

    /**
     * „Çª„É´„ÇíÈñã„Åç„Åæ„Åô.
     * @param {Number} x ‰ΩïË°åÁõÆ.
     * @param {Number} y ‰ΩïÂàóÁõÆ.
     */
    open(x, y) {
      // üí£„Å†„Å£„Åü„ÇâÁµÇ‰∫Ü
      if (this.area[x][y].isBomb) {
        alert("Bomb!!!");
        for (let i = 0; i < this.maxX; i++) {
          for (let j = 0; j < this.maxY; j++) {
            this.area[i][j].isOpen = true;
          }
        }
      }

      this.area[x][y].isOpen = true;

      if (this.area[x][y].bombCount !== 0) return;

      // Âë®Âõ≤„Å´Âú∞Èõ∑„Åå„Å™„ÅÑÁ©∫„Åç„Çª„É´„ÇíÈñã„Åè
      // Â∑¶„ÅÆ„Çª„É´
      if (x-1 >= 0 && this.area[x-1][y].autoOpenable()) {
        this.open(x-1, y);
      }
      // Âè≥„ÅÆ„Çª„É´
      if (x+1 <= this.maxX-1 && this.area[x+1][y].autoOpenable()) {
        this.open(x+1, y);
      }
      // ‰∏ä„ÅÆ„Çª„É´
      if (y-1 >= 0 && this.area[x][y-1].autoOpenable()) {
        this.open(x, y-1);
      }
      // ‰∏ã„ÅÆ„Çª„É´
      if (y+1 <= this.maxY-1 && this.area[x][y+1].autoOpenable()) {
        this.open(x, y+1);
      }
    },

    /**
     * „Çª„É´„Å´Êóó„ÇíÁ´ã„Å¶„Åü„Çä„ÄÅÁ´ã„Å¶„Å™„Åã„Å£„Åü„Çä„Åó„Åæ„Åô.
     * @param {Number} x ‰ΩïË°åÁõÆ.
     * @param {Number} y ‰ΩïÂàóÁõÆ.
     */
    mark(x, y) {
      if (!this.area[x][y].isOpen) {
        this.area[x][y].isMark = !this.area[x][y].isMark;
      }
    },

    /**
     * Áõ§Èù¢„Çí„ÇØ„É™„Ç¢„Åó„Åæ„Åô.
     */
    _clearBoard() {
      this.area = [];
      for (let i = 0; i < this.maxX; i++) {
        const row = [];
        for (let j = 0; j < this.maxY; j++) {
          row.push(new Cell({isOpen: true}));
        }
        this.area.push(row);
      }      
    },

    /**
     * Âú∞Èõ∑„ÇíÈÖçÁΩÆ„Åó„Åæ„Åô.
     */
    _layMines() {
      // „É©„É≥„ÉÄ„É†„Å™Êï¥Êï∞ÂÄ§„ÇíËøî„Åô
      const getRandomInt = max => {
        return Math.floor(Math.random() * Math.floor(max));
      }

      for (let i = 0; i < this.numOfBomb; i++) {
        const x = getRandomInt(this.maxX);
        const y = getRandomInt(this.maxY);

        // Êó¢„Å´Âú∞Èõ∑„ÅåÁΩÆ„Åã„Çå„Å¶„ÅÑ„ÇãÂ†¥Âêà„ÄÅÂà•„ÅÆ„Çª„É´„Å´ÁΩÆ„Åè„Çà„ÅÜ„Å´„Åô„Çã
        if (this.area[x][y].isBomb) {
          i--;
          continue;
        }

        this.area[x][y].isBomb = true;

        // Âú∞Èõ∑„Å´Èö£Êé•„Åô„Çã„Çª„É´„ÅÆÂë®Âõ≤„ÅÆÂú∞Èõ∑Êï∞„ÇíÂ¢ó„ÇÑ„Åô
        // ‰∏ä„ÅÆ„Çª„É´
        if (x-1 >= 0) {
          this.area[x-1][y].bombCount++;
          if (y-1 >= 0) this.area[x-1][y-1].bombCount++;
          if (y+1 < this.maxY) this.area[x-1][y+1].bombCount++;
        }

        // Ê®™„ÅÆ„Çª„É´
        if (y-1 >= 0) this.area[x][y-1].bombCount++;
        if (y+1 < this.maxY) this.area[x][y+1].bombCount++;

        // ‰∏ã„ÅÆ„Çª„É´
        if (x+1 < this.maxX) {
          this.area[x+1][y].bombCount++;
          if (y-1 >= 0) this.area[x+1][y-1].bombCount++;
          if (y+1 < this.maxY) this.area[x+1][y+1].bombCount++;
        }
      }      
    }
  }
}
</script>

<style scoped>
.start-button {
  background-color: #f5f5f5;
  border-radius: 8px;
  color: #333;
  text-decoration: none;
  margin: 2px;
  width: 8rem;
}

table.minesweeper-board {
  margin: 0 auto;
}

.cell {
  background-color: #e3e3e3;
  width: 30px;
  height: 30px;
  user-select: none;
}

.cell--opened {
  background-color: #c0c0c0;
}

.cell--started {
  cursor: pointer;
}

.cell--unstarted {
  background-color: #a9a9a9;
}
</style>